<template>
  <div>
    <Toast />
    <div id="navbar" class="flex align-items-center justify-content-between">
      <div class="flex justify-content-between">
        <ToggleButton
          id="btn-panel"
          v-model="panelActivated"
          :onIcon="panelIcon"
          :offIcon="panelIcon"
        />
        <SelectButton
          class="flex justify-content-between"
          v-model="selectedNavOption"
          :options="navOptions"
          dataKey="name"
          optionValue="name"
        >
          <template id="selectbutton" #option="slotProps">
            <i :class="slotProps.option.icon + ' mdi-4vh'"></i>
          </template>
        </SelectButton>
      </div>
    </div>
    <div id="upload-panel" v-if="uploadActivated">
      <div id="file-upload">
        <Button
          icon="pi pi-times"
          class="p-button-rounded p-button-text align-self-end mb-2"
          @click="uploadActivated = false"
        />
        <FileUpload
          :customUpload="true"
          @uploader="fileHandler"
          accept=".csv, .json, .geojson"
          chooseLabel="Choisir"
          uploadLabel="Ajouter"
          cancelLabel="Annuler"
        >
          <template #empty>
            <p>Glisser-déposer...</p>
          </template>
        </FileUpload>
      </div>
    </div>
    <div id="map-container">
      <div
        id="map"
        class="h-full"
        :style="{
          cursor:
            this.selectedNavOption === 'pinplus'
              ? `url(${require('./assets/' +
                  this.userInput.type.img)}) 20 20, move`
              : '',
        }"
      ></div>
      <div id="calque">
        <div class="card">
          <ToggleButton
            id="btn-calque"
            v-model="layersActivated"
            :onIcon="layerIcon"
            :offIcon="layerIcon"
          />
        </div>
        <div id="layers-table" v-if="layersActivated">
          <DataTable
            class="p-datatable-sm"
            :value="layers"
            v-model:expandedRows="expandedRows"
            v-model:selection="selectedLayer"
            selectionMode="single"
            :scrollable="true"
            scrollHeight="80vh"
            style="min-width: 22vw"
          >
            <template #header>
              <div class="flex justify-content-between align-items-center">
                <span class="p-float-label mt-1">
                  <InputText
                    id="layers-name"
                    type="text"
                    class=""
                    v-model="layerName"
                  />
                  <label for="layers-name">Nom</label>
                </span>
                <Button icon="mdi mdi-layers-plus" @click="addLayer()" />
              </div>
            </template>

            <template #empty>Aucun calque créé.</template>
            <Column :expander="true" headerStyle="width: 3rem" />
            <Column field="name" header="Nom" />
            <Column field="subLayers" header="Objets">
              <template #body="slotProps">
                {{ slotProps.data.subLayers.length }}
              </template>
            </Column>
            <Column header="Visibilité">
              <template #body="slotProps">
                <ToggleButton
                  class="mr-2"
                  v-model="slotProps.data.visible"
                  @click="toggleLayerVisibility(slotProps.data)"
                  onIcon="pi pi-eye"
                  offIcon="pi pi-eye-slash"
                />
                <Button
                  icon="pi pi-trash"
                  @click="removeLayer(slotProps.data)"
                />
              </template>
            </Column>
            <template #expansion="slotProps">
              <div class="w-full">
                <DataTable
                  class="p-datatable-sm"
                  :value="slotProps.data.subLayers"
                  @row-click="putInfoInPanel"
                >
                  <template #empty>Aucun objet créé dans ce calque.</template>
                  <Column field="name" header="Nom" />
                  <Column field="color" header="Couleur">
                    <template #body="slotProps">
                      <Button
                        v-bind:style="{
                          backgroundColor: `#${slotProps.data.color}`,
                        }"
                      />
                    </template>
                  </Column>
                  <Column header="Visibilité">
                    <template #body="subSlotProps">
                      <ToggleButton
                        class="mr-2"
                        v-model="subSlotProps.data.visible"
                        @click="toggleObjectVisibility(subSlotProps.data)"
                        onIcon="pi pi-eye"
                        offIcon="pi pi-eye-slash"
                      />
                      <Button
                        icon="pi pi-trash"
                        @click="removeObject(subSlotProps.data, slotProps.data)"
                      />
                    </template>
                  </Column>
                </DataTable>
              </div>
            </template>
          </DataTable>
        </div>
      </div>
      <Card class="map-panel" v-if="panelActivated">
        <template #title>
          <div class="flex justify-content-between">
            Saisie manuelle
            <Button icon="pi pi-download" @click="uploadActivated = true" />
          </div>
        </template>
        <template #content>
          <div class="flex justify-content-between align-items-center mt-1">
            <span class="p-float-label w-7">
              <InputText
                id="points-name"
                type="text"
                class="max-w-full"
                v-model="userInput.name"
              />
              <label for="points-name">Nom</label>
            </span>
            <ColorPicker v-model="userInput.color" />
            <span>
              <Dropdown
                :options="objectOptions"
                optionLabel="value"
                v-model="userInput.type"
              >
                <template #value="shape">
                  <div :class="shape.value.icon"></div>
                </template>
                <template #option="shape">
                  <div :class="shape.option.icon"></div>
                </template>
              </Dropdown>
            </span>
          </div>
          <h4>Coordonnées</h4>
          <Textarea
            class="no-resize"
            rows="5"
            cols="30"
            v-model="userInput.coords"
          />
          <div class="mt-1 flex justify-content-between align-items-end">
            <div>
              <h5>Aperçu</h5>
              <InputSwitch v-model="userInput.livePreview" />
            </div>
            <div v-if="userInput.type.name === 'pin'">
              <h5>Clustering</h5>
              <InputSwitch v-model="userInput.useClustering" />
            </div>
            <Button
              @click="addObject"
              :label="editionMode ? 'Modifier' : 'Ajouter'"
            />
          </div>
        </template>
      </Card>
    </div>
  </div>
</template>

<script>
import { toRaw } from "vue";
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import "leaflet.markercluster/dist/MarkerCluster.css";
import "leaflet.markercluster/dist/MarkerCluster.Default.css";
import "leaflet.markercluster";

import SidePanel from "./components/SidePanel.vue";

import Parser from "./parser.js";
import Validation from "./validation.js";
import LayerManager from "./layerManager.js";

import "@mdi/font/css/materialdesignicons.css";

// Fixe icônes qui ne s'affichent pas (problème avec Webpack)
// voir https://github.com/PaulLeCam/react-leaflet/issues/453#issuecomment-761806673
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"),
  iconUrl: require("leaflet/dist/images/marker-icon.png"),
  shadowUrl: require("leaflet/dist/images/marker-shadow.png"),
});

export default {
  name: "Map",
  components: {
    SidePanel,
  },
  data() {
    return {
      initialMapOptions: {
        center: [47.01, -8.14],
        zoom: 3,
        preferCanvas: true,
      },
      hasHeaders: true,
      map: null,
      selectedNavOption: "select",
      panelIcon: "mdi mdi-plus mdi-4vh",
      panelActivated: false,
      layersActivated: false,
      uploadActivated: false,
      editionMode: false,
      layerIcon: "mdi mdi-layers-outline mdi-4vh",
      objectOptions: [
        {
          icon: "mdi mdi-map-marker-outline",
          name: "pin",
          img: "map-marker-outline.png",
        },
        {
          icon: "mdi mdi-map-marker-path",
          name: "line",
          img: "map-marker-path.png",
        },
        {
          icon: "mdi mdi-shape-polygon-plus",
          name: "polygon",
          img: "shape-polygon-plus.png",
        },
        {
          icon: "mdi mdi-circle-outline",
          name: "circle",
          img: "circle-outline.png",
        },
      ],
      navOptions: [
        { icon: "mdi mdi-map-marker-plus-outline", name: "pinplus" },
        { icon: "mdi mdi-cursor-move", name: "select" },
      ],
      expandedRows: null,
      previewLayer: null,
      selectedLayer: null,
      layerName: "",
      layers: [
        {
          name: "Layer 1",
          featureGroup: L.featureGroup(),
          subLayers: [],
          visible: true,
        },
        {
          name: "Layer 2",
          featureGroup: L.featureGroup(),
          subLayers: [],
          visible: true,
        },
      ],
      userInput: {
        name: "",
        color: "0d89ec",
        type: {
          icon: "mdi mdi-map-marker-outline",
          name: "pin",
          img: "map-marker-outline.png",
        },
        coords: "",
        livePreview: true,
        useClustering: false,
      },
    };
  },
  methods: {
    initMap: async function () {
      const map = L.map("map", this.initialMapOptions);
      L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        attribution:
          '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
      }).addTo(map);
      map.zoomControl.setPosition("bottomleft");
      map.addLayer(this.layers[0].featureGroup);
      map.addLayer(this.layers[1].featureGroup);
      this.previewLayer = {
        layer: null,
        name: this.userInput.name,
        color: `#${this.userInput.color}`,
        type: this.userInput.type,
        coords: this.userInput.coords,
        visible: true,
      };
      this.selectedLayer = this.layers[0];
      map.on("click", (e) => {
        if (this.selectedNavOption === "pinplus") {
          this.userInput.coords += `${e.latlng.lat}, ${e.latlng.lng}\n`;
        }
      });
      this.map = map;
    },
    resetInput: function () {
      this.userInput.name = "";
      this.userInput.color = "0d89ec";
      this.userInput.type = {
        icon: "mdi mdi-map-marker-outline",
        name: "pin",
        img: "map-marker-outline.png",
      };
      this.userInput.coords = "";
      this.previewLayer = {
        layer: null,
        name: this.userInput.name,
        color: `#${this.userInput.color}`,
        type: this.userInput.type,
        coords: this.userInput.coords,
        visible: true,
      };
      this.editionMode = false;
    },
    putInfoInPanel: function (e) {
      // fitFromLayersTable
      toRaw(this.map).fitBounds(e.data.layer.getBounds(), { maxZoom: 6 });

      this.selectedNavOption = "pinplus";
      this.userInput.name = e.data.name;
      this.userInput.type = e.data.type;
      this.userInput.coords = "";
      e.data.coords.forEach((coord) => {
        this.userInput.coords += coord.toString() + "\n";
      });
      this.userInput.color = e.data.color;

      this.editionMode = true;

      this.previewLayer = this.selectedLayer.subLayers.find(
        (l) => l.name === e.data.name
      );
      this.selectedLayer.subLayers = this.selectedLayer.subLayers.filter(
        (l) => l !== this.previewLayer
      );
    },
    ...LayerManager,
    ...Validation,
    ...Parser,
  },
  mounted() {
    this.initMap();
  },
  computed: {
    formattedCoords() {
      return this.userInput.coords
        .split("\n")
        .filter((l) => /^.+([,;/]\s*).+$/.test(l))
        .map((line) => {
          try {
            return line.split(/[,;/]\s*/).map((c) => this.coordToDegrees(c));
          } catch {
            return undefined;
          }
        })
        .filter((l) => l);
    },
  },
  watch: {
    userInput: {
      handler: function (formInput) {
        if (this.previewLayer.layer) {
          toRaw(this.map).removeLayer(this.previewLayer.layer);
        }
        switch (this.userInput.type.name) {
          case "pin":
            if (this.userInput.useClustering) {
              this.previewLayer.layer = L.markerClusterGroup();
            } else {
              this.previewLayer.layer = L.featureGroup();
            }
            this.formattedCoords.forEach((c) => {
              this.previewLayer.layer.addLayer(
                L.circleMarker(c, { color: `#${formInput.color}` })
              );
            });
            break;
          case "line":
            this.previewLayer.layer = L.polyline(this.formattedCoords, {
              color: `#${formInput.color}`,
            });
            break;
          case "polygon":
            this.previewLayer.layer = L.polygon(this.formattedCoords, {
              color: `#${formInput.color}`,
            });
            break;
          default:
        }
        if (this.userInput.livePreview) {
          toRaw(this.map).addLayer(this.previewLayer.layer);
        }
        this.previewLayer.name = this.userInput.name;
        this.previewLayer.color = `${this.userInput.color}`;
        if (this.formattedCoords.length && this.userInput.livePreview) {
          toRaw(this.map).fitBounds(this.previewLayer.layer.getBounds(), {
            maxZoom: 6,
          });
        }
        this.previewLayer.coords = this.formattedCoords;
        this.previewLayer.type = this.userInput.type;
      },
      deep: true,
    },
  },
};
</script>

<style>
body {
  margin: 0;
  padding: 0;
}

#map-container {
  position: relative;
  height: 93vh;
}

#navbar {
  position: relative;
  height: 7vh;
  min-height: 30px;
}

/*select button*/
#selectbutton {
  height: 100%;
}
#btn-panel {
  height: 100%;
  width: 100%;
  padding: 0.5rem 1rem; /*from button elements*/
  margin-right: 5vw;
  margin-left: 0.5vw;
}
/* icon size*/
.mdi-4vh {
  font-size: 4vh;
}

/*calque */
#btn-calque {
  position: absolute;
  top: 0.7vh;
  right: 0.7vw;
  z-index: 800;
  height: 5vh;
  width: 5vh;
  min-width: 40px;
  min-height: 40px;
}

#calque {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 800;
}

/*layers */
.map-panel {
  z-index: 800;
  position: absolute;
  padding: 1vh;
  top: 0.7vh;
  left: 0.7vw;
}

#layers-table {
  position: absolute;
  z-index: 800;
  background-color: white;
  top: 0.7vh;
  right: 4vw;
  border-radius: 5px;
  margin: 5px;
}

#upload-panel {
  position: absolute;
  background-color: rgba(128, 128, 128, 0.4);
  z-index: 900;
  display: flex;
  width: 100vw;
  height: 93vh;
  align-items: center;
  justify-content: center;
}

#file-upload {
  min-width: 60%;
  padding: 10px;
  background-color: white;
  border: 1px solid #dee2e6;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}

.no-resize {
  resize: none;
}
</style>
