<template>
  <Card class="map-panel opacity-80">
    <template #title>
      <div class="flex justify-content-between">
        {{ fields[type].title }}
        <Button icon="pi pi-download" />
      </div>
    </template>
    <template #content>
      <h4>Coordonnées</h4>
      <Textarea class="no-resize" rows="5" cols="30" />
      <span v-if="fields[type].name" class="p-float-label mt-5">
        <InputText id="points-name" type="text" class="w-full" v-model="inputText" />
        <label for="points-name">{{ fields[type].name }}</label>
      </span>
      <h4 class="mb-0">Personnalisation</h4>
      <div class="mt-1 flex justify-content-between">
        <div v-if="fields[type].size">
          <p>{{ fields[type].size }}</p>
          <InputNumber id="points-size" showButtons mode="decimal" v-model="size"/>
        </div>
        <div v-if="fields[type].fillColor">
          <p>{{ fields[type].fillColor }}</p>
          <ColorPicker class="flex justify-content-center" />
        </div>
      </div>
      <div class="mt-5" v-if="fields[type].lineType">
        <Dropdown :options="fields[type].lineType.options" :placeholder="fields[type].lineType.title" v-model="select"/>
      </div>
    </template>
  </Card>

</template>

<script>
import Card from 'primevue/card';
import Button from 'primevue/button';
import Textarea from 'primevue/textarea';
import InputNumber from 'primevue/inputnumber';
import ColorPicker from 'primevue/colorpicker';
import DropDown from 'primevue/dropdown';

export default {
  name: 'SidePanel',
  data() {
    return {
      fields: {
        pin: {
          title: 'Points',
          textarea: 'Coordonnées',
          name: 'Nom',
          size: 'Taille des points',
          fillColor: 'Couleur'
        },
        polygon: {
          title: 'Polygones',
          textarea: 'Coordonnées',
          name: 'Nom',
          size: 'Taille des polygones',
          fillColor: 'Couleur',
          lineType: {
            title: 'Nature du trait',
            options: [
              'Continu',
              'Pointillé'
            ]
          }
        },
        route: {
          title: 'Route',
          textarea: 'Coordonnées',
          name: 'Nom',
          size: 'Épaisseur du trait',
          fillColor: 'Couleur',
          lineType: {
            title: 'Nature du trait',
            options: [
              'Continu',
              'Pointillé'
            ]
          }
        },
        circle: {
          title: 'Cercles',
          textarea: 'Coordonnées',
          name: 'Nom',
          size: 'Taille des cercles',
          fillColor: 'Couleur'
        }
      },
      inputText: null,
      select: null,
      size: null
    };
  },
  components: {
    Card,
    Button,
    Textarea,
    InputNumber,
    ColorPicker,
    DropDown
  },
  props: ['type']
};
</script>

<style scoped>

.no-resize {
  resize: none;
}

</style>
