import Papa from 'papaparse';
import { toRaw } from 'vue';
import L from 'leaflet';
import { validate } from 'jsonschema';

const REGEX_LAT_HEADER = /^(lat|latitude)$/i;
const REGEX_LNG_HEADER = /^(lng|longitude)$/i;
const REGEX_DD = /^(-?\d{1,3})(?:[,\.](\d+))?(?:[°d]|deg)?$/i;
const REGEX_DD_HEM = /^(\d{1,3})(?:[,\.](\d+))?(?:[°d]|deg)?\s?([NSEWO])$/i;
const REGEX_DMS = /^(\d{1,3})(?:[°d]|deg)\s?(\d{1,2})(?:['’m]|min)\s?(?:(\d{1,2})(?:["s]|sec|''|’’))?\s?([NSEWO])$/i;

/* Corresponds to: [
  [lat1, lng1],
  [lat2, lng2],
  ...
] */
const ARRAY_SCHEMA = {
  type: 'array',
  items: {
    type: 'array',
    items: {
      type: ['number', 'string']
    },
    minItems: 2,
    maxItems: 2
  },
  minItems: 1
};

/* Corresponds to: [
  {
    lat: lat1,
    lng: lng1
  },
  ...
] */
const OBJECT_SCHEMA = {
  type: 'array',
  items: {
    type: 'object',
    patternProperties: {
      '^([lL]at|[lL]atitude|[lL]ng|[lL]ongitude)$': {
        type: ['number', 'string']
      }
    }
  }
};

export default {
  fileHandler: function (event) {
    switch (event.files[0].name.split('.').pop().toLowerCase()) {
      case 'csv':
        this.parseCsvFromFile(event.files[0]);
        this.uploadActivated = false;
        break;
      case 'json':
        this.parseJsonFromFile(event.files[0]);
        this.uploadActivated = false;
        break;
      case 'geojson':
        this.parseGeoJsonFromFile(event.files[0]);
        this.uploadActivated = false;
        break;
      default:
        this.$toast.add({
          severity: 'warn',
          summary: 'Format inconnu.',
          detail: 'Le format de fichier spécifié est non pris en charge.',
          life: 3500
        });
    }
  },
  parseCsvFromFile: function (file) {
    if (this.hasHeaders) {
      Papa.parse(file, {
        dynamicTyping: true,
        header: true,
        skipEmptyLines: true,
        transformHeader: (h) => {
          if (REGEX_LAT_HEADER.test(h))
            return 'lat';
          else if (REGEX_LNG_HEADER.test(h))
            return 'lng';
          else
            return h;
        },
        transform: (v, h) => {
          if (['lat', 'lng'].includes(h)) {
            try {
              return this.coordToDegrees(v);
            } catch {
              this.$toast.add({
                severity: 'error',
                summary: 'Erreur lors de la lecture du fichier.',
                detail: `La valeur ${v} est illisible.`,
                life: 3500
              });
            }
          }
          return v;
        },
        complete: result => this.completeFuncFromObject(result.data)
      });
    } else {
      Papa.parse(file, {
        dynamicTyping: true,
        header: false,
        skipEmptyLines: true,
        transform: v => {
          try {
            return this.coordToDegrees(v);
          } catch {
            this.$toast.add({
              severity: 'error',
              summary: 'Erreur lors de la lecture du fichier.',
              detail: `La valeur ${v} est illisible.`,
              life: 3500
            });
          }
          return v;
        },
        complete: result => this.completeFuncFromArray(result.data)
      });
    }
  },
  parseGeoJsonFromFile: function (file) {
    const reader = new FileReader();
    reader.onload = (event) => {
      L.geoJSON(JSON.parse(event.target.result)).addTo(toRaw(this.map));
    };
    reader.readAsText(file);
  },
  parseJsonFromFile: function (file) {
    const reader = new FileReader();
    reader.onload = (event) => {
      const rawData = JSON.parse(event.target.result);
      if (validate(rawData, ARRAY_SCHEMA).valid) {
        var data = JSON.parse(event.target.result, (k, v) => {
          if (!Array.isArray(v)) {
            try {
              return this.coordToDegrees(v);
            } catch {
              this.$toast.add({
                severity: 'error',
                summary: 'Erreur lors de la lecture du fichier.',
                detail: `La valeur ${v} est illisible.`,
                life: 3500
              });
            }
            return v;
          } else {
            return v;
          }
        });
        this.completeFuncFromArray(data);
      } else if (validate(rawData, OBJECT_SCHEMA).valid) {
        const self = this;
        var data = JSON.parse(event.target.result, function (k, v) {
          if (REGEX_LAT_HEADER.test(k)) {
            try {
              this.lat = self.coordToDegrees(v);
            } catch {
              self.$toast.add({
                severity: 'error',
                summary: 'Erreur lors de la lecture du fichier.',
                detail: `La valeur ${v} est illisible.`,
                life: 3500
              });
              this.lat = v;
            }
          } else if (REGEX_LNG_HEADER.test(k)) {
            try {
              this.lng = self.coordToDegrees(v);
            } catch {
              self.$toast.add({
                severity: 'error',
                summary: 'Erreur lors de la lecture du fichier.',
                detail: `La valeur ${v} est illisible.`,
                life: 3500
              });
              this.lng = v;
            }
          } else {
            return v;
          }
        });
        this.completeFuncFromObject(data);
      }
    };
    reader.readAsText(file);
  },
  coordToDegrees: function (coord) {
    switch (true) {
      case (typeof coord === 'number'):
        return coord;
      case REGEX_DD.test(coord): // ex : -33,135°
        return parseFloat(coord.replace(',', '.'));
      case REGEX_DD_HEM.test(coord): // ex : 133.135deg E
        var [, deg, decimal = 0, hemisphere] = REGEX_DD_HEM.exec(coord);
        return parseFloat(`${/SOW/i.test(hemisphere) ? '-' : ''}${deg}.${decimal}`);
      case REGEX_DMS.test(coord): // ex : 25° 23' 22" S
        var [, deg, min, sec = 0, hemisphere] = REGEX_DMS.exec(coord);
        return eval(`${/SOW/i.test(hemisphere) ? '-' : ''}(${deg} + ${min} / 60 + ${sec} / 3600)`);
      default:
        throw new Error('Conversion impossible.');
    }
  },
  completeFuncFromObject: function (data) {
    if (data.length > 1000) {
      this.userInput.livePreview = false;
    }
    this.userInput.coords = data.map(o => `${o.lat}, ${o.lng}`).join('\n');
  },
  completeFuncFromArray: function (data) {
    if (data.length > 1000) {
      this.userInput.livePreview = false;
    }
    this.userInput.coords = data.map(o => o.join(', ')).join('\n');
  }
};
