const MIN_POINTS = {
  pin: 1,
  line: 2,
  polygon: 3
};

const ERROR_MESSAGES = {
  pin: 'Au moins un point doit être renseigné.',
  line: 'Une ligne doit comporter au moins deux points.',
  polygon: 'Un polygone doit comporter au moins trois points.'
};

export default {
  inputValidation: function () {
    if (!this.layers.length) {
      return {
        error: true,
        summary: 'Pas de calques',
        detail: 'Vous devez d\'abord créer un calque.'
      };
    }
    if (!this.previewLayer.name) {
      return {
        error: true,
        summary: 'Nom invalide',
        detail: 'Le champ "nom" est obligatoire.'
      };
    }
    if (this.selectedLayer.subLayers.find(l => l.name === this.previewLayer.name)) {
      return {
        error: true,
        summary: 'Nom invalide',
        detail: 'Il existe déjà un objet à ce nom dans le calque sélectionné.'
      };
    }
    if (this.formattedCoords.length < MIN_POINTS[this.userInput.type.name]) {
      return {
        error: true,
        summary: 'Coordonnées invalides',
        detail: ERROR_MESSAGES[this.userInput.type.name]
      };
    }
    return {
      error: false
    };
  },
  validateLayer: function () {
    if (!this.layerName) {
      return {
        error: true,
        summary: 'Nom invalide',
        detail: 'Le champ "nom" est obligatoire.'
      };
    }
    if (this.layers.find(l => l.name === this.layerName)) {
      return {
        error: true,
        summary: 'Nom invalide',
        detail: 'Il existe déjà un calque possédant ce nom'
      };
    }
    return {
      error: false
    };
  }
};
