import L from 'leaflet';
import { toRaw } from 'vue';

export default {
  addObject: function () {
    const { error, summary, detail } = this.inputValidation();
    if (error) {
      this.$toast.add({
        severity: 'error',
        summary: summary,
        detail: detail,
        life: 3500
      });
      return;
    }
    toRaw(this.map).removeLayer(this.previewLayer.layer);
    this.previewLayer.layer.on('mouseover', function () {
      this.setStyle({
        weight: 8
      });
    });
    this.previewLayer.layer.on('mouseout', function () {
      this.setStyle({
        weight: 3
      });
    });
    this.previewLayer.layer.on('click', (e) => {
      // find the right sublayer in the selected layer
      this.previewLayer = this.selectedLayer.subLayers.find(l => l.layer === e.target);
      // remove the right sublayer
      this.selectedLayer.subLayers = this.selectedLayer.subLayers.filter(l => l !== this.previewLayer);
      // put info in the panel
      this.userInput.name = this.previewLayer.name;
      this.userInput.type = this.previewLayer.type;
      this.previewLayer.coords.forEach((coord) => {
        this.userInput.coords += coord.toString() + '\n';
      });
      this.userInput.color = this.previewLayer.color;
      // change button label
      this.editionMode = true;
    });

    this.previewLayer.visible = this.selectedLayer.visible;
    this.selectedLayer.subLayers.push(this.previewLayer);
    this.selectedLayer.featureGroup.addLayer(this.previewLayer.layer);
    this.$toast.add({
      severity: 'success',
      summary: `L'objet "${this.previewLayer.name}" a été ajouté avec succès.`,
      life: 3500
    });
    this.resetInput();
  },
  addLayer: function () {
    const { error, summary, detail } = this.validateLayer();
    if (error) {
      this.$toast.add({
        severity: 'error',
        summary: summary,
        detail: detail,
        life: 3500
      });
    } else {
      const layer = {
        name: this.layerName,
        featureGroup: L.featureGroup(),
        subLayers: [],
        visible: true
      };
      this.layers.push(layer);
      this.map.addLayer(layer.featureGroup);
      this.layerName = '';
      this.selectedLayer = layer;
    }
  },
  removeLayer: function (data) {
    toRaw(this.map).removeLayer(data.featureGroup);
    this.layers.splice(this.layers.indexOf(data), 1);
    if (this.selectedLayer === data && this.layers.length) {
      this.selectedLayer = this.layers[0];
    }
  },
  removeObject: function (object, layer) {
    layer.subLayers.splice(layer.subLayers.indexOf(object), 1);
    layer.featureGroup.removeLayer(object.layer);
  },
  toggleLayerVisibility: function (layer) {
    // inversé car la variable visible est inversée avant le @click par ToggleButton
    if (!layer.visible) {
      toRaw(this.map).removeLayer(layer.featureGroup);
      layer.subLayers.forEach(object => {
        object.visible = false;
      });
    } else {
      toRaw(this.map).addLayer(layer.featureGroup);
      layer.subLayers.forEach(object => {
        object.visible = true;
      });
    }
  },
  toggleObjectVisibility: function (object) {
    // inversé car la variable visible est inversée avant le @click par ToggleButton
    if (!object.visible) {
      toRaw(this.map).removeLayer(object.layer);
    } else {
      toRaw(this.map).addLayer(object.layer);
    }
  }
};
